import React, { useEffect, useCallback } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import Dialog from '../Dialog';
import Gamemodal from '../../components/Gamemodal';
import usePageState from '../../components/usePageState';

import found from '../../assets/picture/.png';
import Npc from '../../assets/1-.png';
import button from '../../assets/picture/-.png';

import kids1 from '../../assets/picture/ming/-/1-.png';
import kids2 from '../../assets/picture/ming/-/2-.png';
import kids3 from '../../assets/picture/ming/-/3-.png';
import kids4 from '../../assets/picture/ming/-/3-2.png';
import kids5 from '../../assets/picture/ming/-/4-.png';
import kids6 from '../../assets/picture/ming/-/5-.png';
import kids7 from '../../assets/picture/ming/-/6-.png';
import kids8 from '../../assets/picture/ming/-/7-.png';

import grandpa1 from '../../assets/picture/grandpa/1-.png';
import grandpa2 from '../../assets/picture/grandpa/2-.png';
import grandpa3 from '../../assets/picture/grandpa/3-.png';
import grandpa4 from '../../assets/picture/grandpa/4-.png';
import grandpa5 from '../../assets/picture/grandpa/5-.png';
import grandpa6 from '../../assets/picture/grandpa/6-.png';

import frog1 from '../../assets/picture/Frog/1-.png';
import frog2 from '../../assets/picture/Frog/2-.png';
import frog3 from '../../assets/picture/Frog/3-.png';

const Login = () => {
  const [pageState, setPageState] = usePageState({
    dialogVisible: false,
    currentDialogIndex: 0,
    imageVisible: false,
    introVisible: false,
    isModalVisible: false
  });

  const { dialogVisible, currentDialogIndex, imageVisible, introVisible, isModalVisible } = pageState;
  const location = useLocation();
  const navigate = useNavigate();
  
  const handleUrlParams = useCallback(() => {
    const params = new URLSearchParams(window.location.search);
    const dialogIndex = params.get('dialogIndex');
    const reset = params.get('reset');
    console.log(params, dialogIndex, reset);
    if (reset === 'true') {
      // 
      setPageState({
        dialogVisible: false,
        currentDialogIndex: 0,
        imageVisible: false,
        introVisible: false,
        isModalVisible: false
      });
      // 
      localStorage.clear();
    } else if (dialogIndex !== null) {
      const index = parseInt(dialogIndex, 10);
      setPageState(prevState => ({
        ...prevState,
        dialogVisible: true,
        currentDialogIndex: index,
      }));
    }
    //  URL 
    window.history.replaceState({}, document.title, window.location.pathname);
  }, [setPageState, location.search]); // 

  useEffect(() => {
    handleUrlParams();
  }, [handleUrlParams]);

  const dialogs = [
    { title: '', content: '' },
    { title: '', content: '', imageUrl: Npc },
    { title: '', content: '', imageUrl: kids1 },
    { title: '', content: '', imageUrl: kids1 },
    { title: '', content: ' ', imageUrl: Npc },
    { title: '', content: '!' },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '...', imageUrl: grandpa2 },
    { title: '', content: '', imageUrl: kids3 },
    { title: '', content: '!' },
    { title: '', content: '...' },
    { title: '', content: '!.........?!', imageUrl: kids5 },
    { title: '', content: '?...!!', imageUrl: grandpa3 },
    { title: '', content: '~', imageUrl: grandpa4 },
    { title: '', content: '' },
    { title: '', content: '' },
    { title: '', content: '?', imageUrl: kids1 },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: 'YA!!!', imageUrl: kids6 },
    //
    { title: '', content: '' },
    { title: '', content: '?!', imageUrl: kids7 },
    { title: '', content: '', imageUrl: grandpa5 },
    //
    { title: '', content: '', imageUrl: grandpa6 },
    { title: '', content: '', imageUrl: grandpa6 },
    { title: '', content: '', imageUrl: grandpa6 },
    { title: '', content: '', imageUrl: grandpa6 },
    { title: '', content: '', imageUrl: kids8 },
    //
    { title: '', content: '...' },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '...', imageUrl: grandpa1 },
    { title: '', content: '...?', imageUrl: kids5 },
    { title: '', content: '! ', imageUrl: grandpa4 },
    { title: '', content: '??', imageUrl: kids2 },
    { title: '', content: '...?', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: kids1 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '...' },
    //
    { title: '', content: '..', imageUrl: kids1 },
    { title: '', content: '', imageUrl: kids1 },
    { title: '', content: '~', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: grandpa4 },
    //-
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: kids1 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: kids6 },
    //
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '~...', imageUrl: grandpa6 },
    { title: '', content: '...', imageUrl: grandpa6 },
    { title: '', content: '' },
    { title: '', content: '', imageUrl: kids4 },
    { title: '', content: '?', imageUrl: grandpa3 },
    { title: '', content: '', imageUrl: kids6 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '' },
    { title: '', content: '' },
    //
    { title: '', content: '', imageUrl: kids6 },
    { title: '', content: '', imageUrl: frog1 },
    { title: '', content: '!', imageUrl: kids1 },
    { title: '', content: '', imageUrl: frog2 },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: frog3 },
    { title: '', content: '?', imageUrl: frog3 },
    { title: '', content: '?', imageUrl: kids6 },
    { title: '', content: '......', imageUrl: grandpa6 },
    { title: '', content: '......', imageUrl: grandpa6 },
    { title: '', content: '......', imageUrl: grandpa6 },
    { title: '', content: '...', imageUrl: kids4 },
    { title: '', content: '', imageUrl: kids4 },
    { title: '', content: '' },
    { title: '', content: '', imageUrl: kids5 },
    { title: '', content: '!', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: frog3 },
    { title: '', content: '' },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: '!', imageUrl: kids4 },
    { title: '', content: '', imageUrl: kids4 },
    { title: '', content: '', imageUrl: frog3 },
    { title: '', content: '' },
    { title: '', content: '' },
    { title: '', content: '' },
    { title: '', content: '' },
    { title: '', content: '' },
    { title: '', content: '' },
    //?###
  ];

  const setCurrentDialogIndex = useCallback((index) => {
    setPageState((prevState) => ({
      ...prevState,
      currentDialogIndex: index
    }));
  }, [setPageState]);

  useEffect(() => {
    const params = new URLSearchParams(location.search);
    const dialogIndex = params.get('dialogIndex');
    if (dialogIndex !== null) {
      setPageState((prevState) => ({
        ...prevState,
        dialogVisible: true,
        currentDialogIndex: parseInt(dialogIndex, 10),
      }));
    }
  }, [location.search]);

  useEffect(() => {
    if (dialogVisible) {
      window.history.pushState(null, '', `?dialogIndex=${currentDialogIndex}`);
    }
  }, [currentDialogIndex, dialogVisible]);

  const openDialog = useCallback(() => {
    setPageState((prevState) => ({
      ...prevState,
      introVisible: true,
    }));
  }, [setPageState]);

  const showDialog = useCallback(() => {
    setPageState((prevState) => ({
      ...prevState,
      introVisible: false,
      dialogVisible: true,
      imageVisible: true,
    }));
  }, [setPageState]);

  const closeDialog = useCallback(() => {
    setPageState((prevState) => ({
      ...prevState,
      dialogVisible: false,
      currentDialogIndex: 0,
      imageVisible: false,
    }));
    navigate('/');
  }, [setPageState, navigate]);

  const handlePreviousDialog = useCallback(() => {
    if (currentDialogIndex > 0) {
      setPageState((prevState) => ({
        ...prevState,
        currentDialogIndex: prevState.currentDialogIndex - 1,
      }));
    }
  }, [currentDialogIndex, setPageState]);

  const handleNextDialog = useCallback(() => {
    if (currentDialogIndex < dialogs.length - 1) {
      setPageState((prevState) => ({
        ...prevState,
        currentDialogIndex: prevState.currentDialogIndex + 1,
      }));
    }
  }, [currentDialogIndex, dialogs.length, setPageState]);

  const showModal = useCallback(() => {
    setPageState((prevState) => ({
      ...prevState,
      isModalVisible: true,
    }));
  }, [setPageState]);

  const handleModalCancel = useCallback(() => {
    setPageState((prevState) => ({
      ...prevState,
      isModalVisible: false,
    }));
  }, [setPageState]);

  return (
    <div className='dialog'>
      {!introVisible &&
        <Gamemodal
          isVisible={isModalVisible}
          showModal={showModal}
          handleModalCancel={handleModalCancel}
        />}
      {dialogVisible && (
        <Dialog
          visible={dialogVisible}
          onClose={closeDialog}
          dialogData={dialogs}
          currentIndex={currentDialogIndex}
          onPrevious={handlePreviousDialog}
          onNext={handleNextDialog}
          setCurrentDialogIndex={setCurrentDialogIndex}
        />
      )}
      {!dialogVisible && !introVisible && (
        <div className="centered-container">
          <img src={found} alt="Placeholder Image" className="centered-image" />
          <button type="button" onClick={openDialog} className="startButton">
            <img src={button} alt="Start Button" />
          </button>
        </div>
      )}
      {introVisible && (
        <div className="intro-container">
          <p>
            
            
            
            
          </p>
          <button onClick={showDialog}></button>
        </div>
      )}
    </div>
  );
};

export default Login;