import React, { useCallback, useRef } from 'react';
import { Button, Modal } from 'antd';
import { EllipsisOutlined } from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import usePageState from './usePageState';

const ModalButton = ({ isVisible, showModal, handleModalCancel }) => {
    const navigate = useNavigate();
    const [pageState, setPageState] = usePageState({
        dialogVisible: false,
        currentDialogIndex: 0,
    });
    const formRef = useRef(null);

    const navigateToGame = useCallback((gameNumber, dialogIndex = null) => {
        setPageState(prevState => ({
            ...prevState,
            currentDialogIndex: dialogIndex || prevState.currentDialogIndex
        }));
        handleModalCancel();
        navigate(`/game${gameNumber}`);
    }, [setPageState, handleModalCancel, navigate]);

    const navigateToStory = useCallback((dialogIndex) => {
        setPageState(prevState => ({
            ...prevState,
            dialogVisible: true,
            currentDialogIndex: dialogIndex
        }));
        handleModalCancel();
        navigate('/?dialogIndex=' + dialogIndex);
    }, [setPageState, handleModalCancel, navigate]);

    const clearLocalStorage = useCallback(() => {
        localStorage.clear();
        setPageState({
            dialogVisible: false,
            currentDialogIndex: 0,
            introVisible: false,
            imageVisible: false,
            isModalVisible: false
        });
        alert('');
        handleModalCancel();
        // 
        if (formRef.current) {
            formRef.current.submit();
        }
    }, [setPageState, handleModalCancel]);


    return (
        <>
            <div style={{ position: 'relative', width: '100%', height: '100%' }}>
                <Button
                    type="primary"
                    onClick={showModal}
                    style={{
                        position: 'absolute', top: '0', left: '0', transform: 'translate(-45vw, -20vw)', zIndex: 1000,
                        backgroundColor: '#8b5a2b',
                        borderColor: '#8b5a2b',
                        color: 'white'
                    }}
                >
                    <EllipsisOutlined />
                </Button>
            </div>
            <Modal
                title=""
                open={isVisible}
                onCancel={handleModalCancel}
                footer={null}
            >
                <div style={{ display: 'flex', flexDirection: 'column', gap: '10px' }}>
                    <Button onClick={() => navigateToGame(1)}></Button>
                    <Button onClick={() => navigateToStory(5)}>-1 </Button>
                    <Button onClick={() => navigateToGame(2)}>-2</Button>
                    <Button onClick={() => navigateToStory(10)}>-2</Button>
                    <Button onClick={() => navigateToGame(3)}></Button>
                    <Button onClick={() => navigateToStory(22)}></Button>
                    <Button onClick={() => navigateToGame(4)}></Button>
                    <Button onClick={() => navigateToStory(37)}></Button>
                    <Button onClick={() => navigateToGame(5)}>AR</Button>
                    <Button onClick={() => navigateToStory(38)}></Button>
                    <Button onClick={() => navigateToGame(6)}></Button>
                    <Button onClick={() => navigateToStory(48)}>-1</Button>
                    <Button onClick={() => navigateToGame(7)}></Button>
                    <Button onClick={() => navigateToStory(68)}>-2</Button>
                    <Button onClick={clearLocalStorage} style={{ backgroundColor: 'red', color: 'white' }}></Button>
                </div>
            </Modal>
            <form ref={formRef} action="/" method="GET" style={{ display: 'none' }}>
                <input type="hidden" name="reset" value="true" />
            </form>
        </>
    );
};

export default ModalButton;
